import { usersStudio, videos, circles, circle, questions, travels, bindfans, visitors } from '@/http/api.js';
import tuiLoadmore from '@/plugins/thorui/components/loadmore/loadmore.vue';
import tuiNomore from '@/plugins/thorui/components/nomore/nomore';
// import dragball from '@/plugins/drag-ball/components/drag-ball/drag-ball.vue';
export default {
	components: {
		tuiLoadmore,
		tuiNomore,
		// dragball
	},
	data() {
		return {
			id: '', //详情id
			openid: '',
			topnocontent: false, //是否有我的定制师
			pagea: 1,
			pageb: 1,
			pagec: 1,
			paged: 1,
			loadding: false,
			pullUpOn: true,
			studioBottomActive: 1, //按钮选中状态
			answer: '', //回答
			fixTop: '', //区域离顶部的高度
			scrollTop: 0, //滑动条离顶部的距离
			studioBottomList: [
				{
					id: 1,
					img: '../../static/img/video.png',
					title: '短视频'
				},
				// {
				// 	id: 2,
				// 	img: '../../static/img/camera.png',
				// 	title: '直播'
				// },
				{
					id: 3,
					img: '../../static/img/people.png',
					title: '我的圈子'
				},
				{
					id: 4,
					img: '../../static/img/city.png',
					title: '攻略游记'
				},
				{
					id: 5,
					img: '../../static/img/speak.png',
					title: '问答'
				}
			],
			// 问答
			dialogue: [],
			// 短视频
			video: [],
			// 直播
			live: [
				{
					id: 1,
					img: '../../static/img/livea.jpg',
					status: 0
				},
				{
					id: 2,
					img: '../../static/img/liveb.jpg',
					status: 1
				},
				{
					id: 3,
					img: '../../static/img/livec.jpg',
					status: 2
				},
				{
					id: 4,
					img: '../../static/img/lived.jpg',
					status: 1
				}
			],
			// 攻略游记
			travels: [],
			// 我的圈子推荐
			recommendStudiotop: [],
			// 我的圈子
			recommendStudio: [],
			data: {
				user: {
					id: 0,
					supplier_id: 0,
					username: '',
					truename: '',
					mobile: '',
					avatar: '',
					nickname: '',
					sex: 2,
					wx_number: '',
					profile: '',
					rating: '',
					cover_image: '',
					role: '',
					role_zh: '',
					role_type: '',
					is_admin: 0
				},
				trade_number: 0,
				visitor_number: 0,
				service_number: 0,
				play_number: 0,
				plays: [],
				isDis: 0,
				uid: '',
				code: '',
				openid: '',
				userInfo: {}
			}
		};
	},
	onShow() {
		const pages = getCurrentPages();
		const currPage = pages[pages.length - 1]; // 当前页
		wx.hideHomeButton();
		if (uni.getStorageSync('code')) {
			this.code = uni.getStorageSync('code');
		}
		if (uni.getStorageSync('openid')) {
			this.openid = uni.getStorageSync('openid');
		}
		if (uni.getStorageSync('userInfo')) {
			this.userInfo = uni.getStorageSync('userInfo');
			this.visitorsfn();
		}
		if (currPage.data.uid) {
			this.uid = currPage.data.uid;
			this.bindfans();
		}
	},
	onPageScroll(res) {
		wx.createSelectorQuery()
			.select('.studioBottom-top')
			.boundingClientRect(rect => {
				this.fixTop = rect.top;
			})
			.exec();
	},
	onLoad(options) {
		this.id = options.id;
		// this.getList();
		if (options.id) {
			uni.setStorageSync('studio', options.id);
		}
		if (options.isDis && options.isDis == 1) {
			this.isDis = 1;
		}
		if (options.uid) {
			this.uid = options.uid;
		}
		if (uni.getStorageSync('code')) {
			this.code = uni.getStorageSync('code');
		}
		if (uni.getStorageSync('openid')) {
			this.openid = uni.getStorageSync('openid');
		}
		if (uni.getStorageSync('userInfo')) {
			this.userInfo = uni.getStorageSync('userInfo');
		}
		if (getCurrentPages().length == 1) {
			// if (!uni.getStorageSync('token')) {
			// 	uni.navigateTo({
			// 		url: `/pages/authorizations/authorizations?id=${this.id}&needUserInfo=${1}&needToken=${1}`
			// 	});
			// }
			if (!uni.getStorageSync('userInfo')) {
				uni.navigateTo({
					url: `/pages/authorizations/authorizations?id=${this.id}&needUserInfo=${1}&needToken=${0}`
				});
			} else {
				this.bindfans();
				this.visitorsfn();
			}
		} else {
			if (!uni.getStorageSync('userInfo')) {
				uni.navigateTo({
					url: `/pages/authorizations/authorizations?id=${this.id}&needUserInfo=${1}&needToken=${0}`
				});
			} else {
				this.visitorsfn();
			}

			// wx.getSetting({
			// 	success: res => {
			// 		//判断是否授权，如果授权成功
			// 		if (res.authSetting['scope.userInfo']) {
			// 			//获取用户信息
			// 			wx.getUserInfo({
			// 				success: res => {
			// 					this.userInfo = res.userInfo;
			// 					uni.setStorageSync('userInfo', res.userInfo);
			// 					this.getDetail();
			// 				}
			// 			});
			// 		} else {
			// 			this.authorizations();
			// 			return;
			// 		}
			// 	}
			// });
		}

		this.getDetail();
		this.videos();
		this.circle();
		this.circles(true);
		this.travelsfn();
		this.questions();
	},
	methods: {
		// 访客记录
		visitorsfn() {
			visitors({ openid: this.openid, user_id: this.id, userInfo: this.userInfo }).then(res => {});
		},
		//进入其他工作室
		gotoStudio(friend_id) {
			uni.navigateTo({
				url: '/pages/studio/studio?id=' + friend_id
			});
		},
		//拨打电话
		tel() {
			wx.makePhoneCall({
				phoneNumber: this.data.user.mobile
			});
		},
		//走进他的生活
		gotolife() {
			wx.pageScrollTo({
				scrollTop: 820,
				duration: 300
			});
		},
		// 绑定粉丝
		bindfans() {
			bindfans('', this.uid, this.code, this.openid, this.userInfo).then(res => {
				// this.list = res.data;
				console.log(res);
				if (res.code == 0) {
					// uni.showToast({
					// 	icon: 'none',
					// 	title: '绑定粉丝成功'
					// });
				}
			});
		},

		// 用户信息
		getDetail() {
			usersStudio(this.id, this.openid).then(res => {
				this.data = res.data;
			});
		},
		// 短视频
		videos() {
			videos(this.pagea, this.id).then(res => {
				this.loadding = true;
				if (res.data.data.length == 0) {
					this.loadding = false;
					this.pullUpOn = false;
					return;
				}
				if (res.data.data.length !== 0) {
					this.loadding = false;
					this.pullUpOn = false;
					this.video = this.video.concat(res.data.data);
					this.pagea++;
					return;
				}
			});
		},
		//我的圈子推荐
		circles(recommend) {
			circles(this.id, recommend).then(res => {
				this.recommendStudiotop = res.data.slice(0, 1);
			});
		},
		// 我的圈子
		circle() {
			circle(this.pageb, this.id).then(res => {
				this.loadding = true;
				if (res.data.data.length == 0) {
					this.loadding = false;
					this.pullUpOn = false;
					return;
				}
				if (res.data.data.length !== 0) {
					this.loadding = false;
					this.pullUpOn = false;
					this.recommendStudio = this.recommendStudio.concat(res.data.data);
					this.pageb++;
					return;
				}
			});
		},
		travelsfn() {
			travels(this.pagec, this.id).then(res => {
				this.loadding = true;
				if (res.data.data.length == 0) {
					this.loadding = false;
					this.pullUpOn = false;
					return;
				}
				if (res.data.data.length !== 0) {
					this.loadding = false;
					this.pullUpOn = false;
					let travelsarr = res.data.data;
					for (let i = 0; i < travelsarr.length; i++) {
						for (let y = 0; y < travelsarr[i].content.length; y++) {
							if (travelsarr[i].content[y].indexOf('http') != -1) {
								travelsarr[i].content.splice(y, 1);
							}
						}
					}
					this.travels = this.travels.concat(travelsarr);
					this.pagec++;
					return;
				}
			});
		},
		// 问答
		questions() {
			questions(this.paged, this.id).then(res => {
				this.loadding = true;
				if (res.data.data.length == 0) {
					this.loadding = false;
					this.pullUpOn = false;
					return;
				}
				if (res.data.data.length !== 0) {
					this.loadding = false;
					this.pullUpOn = false;
					this.dialogue = this.dialogue.concat(res.data.data);
					this.paged++;
					return;
				}
			});
		},
		studioActive(active) {
			this.studioBottomActive = active;
			if (active == 1) {
				this.pagea = 1;
				this.video = [];
				this.videos();
			}
			if (active == 3) {
				this.pageb = 1;
				this.recommendStudio = [];
				this.circle();
			}
			if (active == 4) {
				this.pagec = 1;
				this.travels = [];
				this.travelsfn();
			}
			if (active == 5) {
				this.paged = 1;
				this.dialogue = [];
				this.questions();
			}
		},
		toDetail(id, type) {
			switch (type) {
				case 'hotel':
					uni.navigateTo({
						url: `/pages/details/hotelDetail/hotelDetail?id=${id}&isDis=1`
					});
					break;
				case 'repast':
					uni.navigateTo({
						url: `/pages/details/restaurantDetail/restaurantDetail?id=${id}&isDis=1`
					});
					break;
				case 'sight':
					uni.navigateTo({
						url: `/pages/details/scenicSpotDetail/scenicSpotDetail?id=${id}&isDis=1`
					});
					break;
				case 'homestay':
					uni.navigateTo({
						url: `/pages/details/homestayDetail/homestayDetail?id=${id}&isDis=1`
					});
					break;
				// case 'feature':
				// 	uni.navigateTo({
				// 		url: `/pages/details/otherDetail/otherDetail?id=${id}&isDis=1`
				// 	});
				// 	break;
				case 'specialty':
					uni.navigateTo({
						url: `/pages/details/otherDetail/otherDetail?id=${id}&isDis=1`
					});
					break;
				case 'boutique':
					uni.navigateTo({
						url: `/pages/details/standardDetail/standardDetail?id=${id}&isDis=1`
					});
					break;
			}
		},
		gotoscenicRecommend() {
			uni.navigateTo({
				url: '/pages/scenicRecommend/scenicRecommend?id=' + this.id
			});
		},
		//短视频详情
		toVodeo(id) {
			uni.navigateTo({
				url: '/pages/newDetail/videodetails/videodetails?id=' + id
			});
		},
		//游记详情
		toTraval(id) {
			uni.navigateTo({
				url: '/pages/newDetail/traveldetails/traveldetails?id=' + id
			});
		},
		//问答详情
		toQuestion(id) {
			uni.navigateTo({
				url: '/pages/newDetail/questionsdetails/questionsdetails?id=' + id
			});
		}
	},
	// 页面上拉触底事件的处理函数
	onReachBottom: function() {
		setTimeout(() => {
			if (this.studioBottomActive == 1) {
				this.loadding = false;
				this.pullUpOn = false;
				this.videos();
			}
			if (this.studioBottomActive == 3) {
				this.loadding = false;
				this.pullUpOn = false;
				this.circle();
			}
			if (this.studioBottomActive == 4) {
				this.loadding = false;
				this.pullUpOn = false;
				this.travelsfn();
			}
			if (this.studioBottomActive == 5) {
				this.loadding = false;
				this.pullUpOn = false;
				this.questions();
			}
		}, 1000);
	}
};
